import Header from "@/components/common/Header";

export default {
  name: "",
  props: [""],
  data() {
    return {
      TitleObjData: {
        titleContent: "",
        showLeftBack: true,
        showRightMore: false
      },
      contentList: [
        {
          title: "卡戳app10.0上线",
          content:
            "卡戳app10.0全新功能开启，全新UI设计卡戳app10.0全新功能开启，全新UI设计卡戳app10.0全新功能开启，全新UI设计卡戳app10.0全新功能开启，全新UI设计",
          date: "2019-3-11"
        },
        {
          title: "卡戳app10.0上线",
          content: "卡戳app10.0全新功能开启，全新UI设计",
          date: "2019-3-11"
        },
        {
          title: "卡戳app10.0上线",
          content: "卡戳app10.0全新功能开启，全新UI设计",
          date: "2019-3-11"
        }
      ]
    };
  },

  components: {
    Header
  },

  computed: {},
  created() {
    this.setHeaderTitle();
  },
  beforeMount() {},

  mounted() {},

  methods: {
    setHeaderTitle() {
      switch (this.$route.query.title) {
        case "systemannounce":
          this.TitleObjData.titleContent = "系统公告";
          break;
        case "ordernotice":
          this.TitleObjData.titleContent = "订单通知";
          break;
        case "usermessage":
          this.TitleObjData.titleContent = "个人消息";
          break;
        default:
          this.TitleObjData.titleContent = "";
      }
    }
  },

  watch: {}
};



